*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Roboto Condensed", sans-serif;
}
body::-webkit-scrollbar {
  display: none;
}

.navbar{
    position: relative;
    display: flex;
    background: black;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 100vw;
}
.sticky{
    position: sticky;
    top: 0; 
    z-index: 999;
}
.logo img{
    margin: 2px 40px;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    border: 2px solid white;
}
.nav{
    height: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
}
.nav-links{
    cursor: pointer;
    font-family: 'Roboto Condensed', sans-serif;
    margin: 0px 20px;
    padding: 5px 10px;
    text-decoration: none;
    border-radius: 10px;
    color: white;
    font-size: larger;
    transition: background 0.4s ease-in-out;

}
.nav-links:hover{
    background: hsl(0, 0%, 20%);
    color: hsl(0, 0%, 70%);
}
.active{
    background: hsl(0, 0%, 20%);
}
li{
    list-style: none;
}
.name{
    display: flex;
    color: white;
    justify-content: center;
    align-items: center;
    margin: 0px 50px;
    font-size:larger ;
    font-family: 'Roboto Condensed', sans-serif;
}
.head-nav{
    display: flex;
    max-width: 100vw;

}
.arrow{
    color: white;
    display: none;
}
@media only screen and (max-width: 750px) {
   .nav-responsive{
       position: absolute;
       top: 60px;
       z-index: 100;
    }
    .logo img {
        margin: 2px 20px;
    }
    .arrow{
        display: block;
        font-size: 30px;
    }
    .logo{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .nav{
       width: 100vw;
       position: absolute;
       flex-direction: row;
       background: black;
       transition: left 1s;
       opacity: 0.9;
       height: 85vh;
       left: -600px;
       z-index: 100;
    }
    .new{
       left: 0px;
       transition: left 1s;
   }
   .nav-links{
       margin: 20px 100px;
       z-index: 100;
   }
       
}
.header {
  max-width: 100vw;
  margin: 10px;
  font-family: "Roboto Condensed", sans-serif;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}
.info {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  padding: 20px;
}
.info h3 {
  color: hsl(0, 0%, 40%);
  font-size: 25px;
}
.info h1 {
  font-size: 45px;
  font-weight: bold;
  background: #cf0a0a;
  background: linear-gradient(to right, #cf0a0a 0%, #0007cf 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.info h2{
    font-size: 35px;
    text-align: left;
    font-weight: lighter;
    color: hsl(240, 100%, 60%);
}
.image{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  width: 45vw;
  animation: animate 3s infinite alternate-reverse;

}
@-webkit-keyframes animate{
  0%{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  100%{
    -webkit-transform: translateY(10px);
            transform: translateY(10px);

  }
}
@keyframes animate{
  0%{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  100%{
    -webkit-transform: translateY(10px);
            transform: translateY(10px);

  }
}
.image img{
  height: 80vh;
  width: 45vw;
}
.icons{
  margin-top: 15px;
}
.btn{
  margin-top: 30px;
}

@media only screen and (max-width: 800px) {
  .image img{
    height: 30vh;
    width: 90vw;
  }
  .image{
    height: 30vh;
    width: 90vw;
  }
}
i{
    margin: 5px;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}
i:hover{
    color: hsl(0, 0%, 20%);
    transition: background 0.4s ease-in-out;   
    -webkit-transform: translateY(-10px);   
            transform: translateY(-10px);
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}
a{
    color: hsl(0deg 0% 30%);
